











































































import tabBar from '../components/tabBar'
export default {
	data() {
		return {
			title: '',
			address:'',
			list:[      // 免费领券
				{'couponMoney':'10','couponTxt':'条件优惠券','couponUse':'满20使用','couponTime':'2019-04-09-2025-04-10'},
				{'couponMoney':'10','couponTxt':'10元代金券','couponUse':'无门槛','couponTime':'2019-04-09-2022-04-10'}
			],
			hasSetBg:false
		}
	},
	components:{
		tabBar
	},
	onLoad(options) {
		this.address = uni.getStorageSync('address');
		uni.removeStorageSync('address');
		this.title = uni.getStorageSync('title');
		uni.removeStorageSync('title');
		console.log(this.title);
		this.setBg();
	},
	methods: {
		setBg() {
			this.hasSetBg = !this.hasSetBg;
			uni.setNavigationBarColor({
				frontColor: this.hasSetBg ? "#ffffff" : "#000000",
				backgroundColor: this.hasSetBg ? "#46B2FF" : "#F8F8F8"
			})
		},
		toGetUserInfo(){  // 获取用户信息
			uni.navigateTo({
				url:'../API/get-user-info/get-user-info'
			})
		},
		toGetLogin(){   // 授权登录
		},
		toList(){  // 返回门店列表
			uni.redirectTo({
				url:'../index'
			});
			uni.hideTabBar();
		},
		toClickMenu(){  // 去点餐
			console.log('去点餐');
			uni.navigateTo({
				url:'../shopMall/index'
			})
		},
		toPay(){  // 去买单
			console.log('去买单');
			uni.switchTab({
				url:'../check/index'
			})
		},
		toMy(){
			uni.navigateTo({
				url:'../my/index'
			})
		},
		toHome(){
			uni.navigateTo({
				url:'../home/home'
			})
		},
		getVal(msg){
			cosnole.log(msg);
		},
		toScanCode(){
			uni.scanCode({
				success: function (res) {
					console.log('条码类型：' + res.scanType);
					console.log('条码内容：' + res.result);
				}
			});
		},
		toGetLocation(){
			uni.getLocation({
				type: 'wgs84',
				success: function (res) {
					console.log('当前位置的经度：' + res.longitude);
					console.log('当前位置的纬度：' + res.latitude);
				}
			});
		},
		toMyCoupon(){   // 去我的优惠券
			uni.navigateTo({
				url:'../my/coupon'
			})
		},
		toMyCard(){   // 我的会员卡
			uni.navigateTo({
				url:'../my/card'
			})
		}
	}
}
